/** Paginas Internas **/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  text-transform: uppercase;
  margin: 0.5rem 0;
}
.alerta.error {
  background-color: #c70404;
}
.alerta.exito {
  background-color: #08a802;
}

.summary {
  color: #ffffff;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 1.8rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.summary .psum {
  font-size: 1.6rem;
  text-decoration: dashed;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
  color: #ffffff;
}
table.propiedades thead {
  background-color: #08a802;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades .imagen-tabla {
  width: 20rem;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.boton-verde-block, .boton-amarillo-block {
  margin: 2rem 0;
}

.boton-verde {
  margin: 2rem 0;
  margin-bottom: 0;
}

.imagen-small {
  width: 50rem;
}